.translate, .translate-y-40, .translate-x--50 {
  --x: 0;
  --y: 0;
  transform: translate(var(--x), var(--y));
}
.translate-x--50 {
  --x: -50%;
}
.translate-y-40 {
  --y: 40%;
}

.shadow, .shadow-400, .shadow-300, .shadow-100, .shadow-b-lg, .shadow-b-md, .shadow-y-sm {
  box-shadow: var(--shadow-x, 0) var(--shadow-y, 0) var(--shadow-blur, 0px) var(--shadow-spread, 0px) var(--shadow-color, #000);
}
.shadow-y-sm {
  --shadow-y: 3px;
}
.shadow-b-md {
  --shadow-blur: 10px;
}
.shadow-b-lg {
  --shadow-blur: 20px;
}
