  .header {
    padding: 4rem;
  }
}
.header-logo {
  width: 200px;
}
@media (min-width: 1280px) {
  .header .btn {
    padding-inline: 4rem;
    padding-block: 0.5rem;
  }
}

.hero {
  background: url("../images/bg-hero-mobile.svg") no-repeat, var(--clr-neutral-100);
  background-size: cover;
}
@media (min-width: 1280px) {
  .hero {
    background: url("../images/bg-hero-desktop.svg") no-repeat, var(--clr-neutral-100);
    display: grid;
    grid-template-columns: 1fr 1.1fr;
    gap: 4rem;
  }
}
.hero img {
  width: min(100%, 800px);
}
@media (min-width: 1280px) {
  .hero-intro {
    align-items: flex-start;
    text-align: start;
  }
}
@media (min-width: 1280px) {
  .hero-intro h1 {
    font-size: 2.4rem;
  }
}
.hero-intro p {
  font-size: 1.78rem;
}
@media (min-width: 1280px) {
  .hero-intro p {
    font-size: 1.1rem;
  }
}
.hero-intro .btn {
  font-size: 1rem;
  padding-inline: 4rem;
}

.card {
  border-radius: 1.2rem;
}

.end-card {
  width: clamp(675px, 90%, 950px);
}
@media (min-width: 1280px) {
  .end-card .btn {
    font-size: 1.2rem;
  }
}

@media (min-width: 1024px) {
  .features-section {
    padding-top: 10rem;
    padding-inline: 6rem;
  }
}

@media (min-width: 1024px) {
  .feature-card {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    align-items: center;
    text-align: start;
    padding-right: 2rem;
    gap: 10rem;
  }
}
.feature-card-image {
  grid-column: -2;
  grid-row: 1;
}
.feature-card-description {
  grid-column: 1;
  grid-row: 1;
}
@media (min-width: 1024px) {
  .feature-card-description h2 {
    margin-top: 0;
  }
}
.feature-card-description p {
  font-size: 1rem;
}
@media (min-width: 1024px) {
  .feature-card[data-reversed] .feature-card-image {
    grid-column: 1;
  }
  .feature-card[data-reversed] .feature-card-description {
    grid-column: -2;
  }
}

.footer {
  color: white;
}
@media (min-width: 1280px) {
  .footer {
    padding-left: 6rem;
    padding-right: 1rem;
    padding-bottom: 4rem;
  }
}
@media (min-width: 1280px) {
  .footer-content {
    display: grid;
    grid-template-columns: 1.5fr 1fr 1.2fr;
    grid-template-columns: 1.2fr auto 1fr;
    gap: 6rem;
  }
}
.footer-logo {
  width: 292px;
  filter: contrast(0%) brightness(200%);
}
.footer-info {
  --font: 1.2rem;
}
@media (min-width: 1280px) {
  .footer-info {
    --font: 0.9rem;
    padding-bottom: 4rem;
  }
}
.footer-info > * {
  font-size: var(--font);
  padding-left: calc(3 * var(--font));
  position: relative;
}
@media (min-width: 1280px) {
  .footer-info > * {
    padding-left: calc(2.4 * var(--font));
  }
}
.footer-info > *::before {
  --icon-mult: 0.7x;
  content: "";
  position: absolute;
  z-index: 10;
  top: calc(0.2 * var(--font));
  left: 0.2rem;
}
@media (min-width: 1280px) {
  .footer-info > *::before {
    --icon-mult: 1x;
  }
}
.footer-info > *.footer-location::before {
  content: image-set(url("../images/icon-location.svg") var(--icon-mult));
}
.footer-info > *.footer-phone::before {
  content: image-set(url("../images/icon-phone.svg") var(--icon-mult));
}
.footer-info > *.footer-email::before {
  content: image-set(url("../images/icon-email.svg") var(--icon-mult));
}
.footer-links {
  font-size: 1.4rem;
}
@media (min-width: 1280px) {
  .footer-links {
    font-size: 1.1rem;
    grid-template-rows: 1fr 1fr 1fr;
    grid-auto-flow: column;
    align-content: start;
    height: fit-content;
    row-gap: 1rem;
    column-gap: 6rem;
  }
}
.footer-links a:hover {
  text-decoration: underline;
}
@media (min-width: 1280px) {
  .footer-end {
    align-items: flex-start;
  }
}
.footer-social {
  font-size: 1.3em;
}
@media (min-width: 1280px) {
  .footer-social {
    font-size: 1rem;
  }
}
.footer-social .social {
  --icon-size: 2.2em;
  border: 1px solid #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--icon-size);
  height: var(--icon-size);
  transition: border-color 0.2s ease-in-out, color 0.2s ease-in-out;
}
.footer-social .social:hover {
  border-color: var(--clr-pink);
  color: var(--clr-pink);
}
.footer-copy {
  font-size: 0.7rem;
}
.footer a {
  color: inherit;
  text-decoration: none;
}